import { transactionService } from "../../services/transactionService";

export const loadTransactions = () => {
  return async (dispatch) => {
    let transactions = await transactionService.getTransactions();
    if (!transactions) transactions = null;

    dispatch({ type: "SET_TRANSFERS", transactions });
  };
};

export const sendCoins = (contactEmail, transactionAmount, loggedUserEmail) => {
  return async (dispatch) => {
    const transaction = await transactionService.addTransaction(
      contactEmail,
      transactionAmount,
      loggedUserEmail
    );
    dispatch({ type: "ADD_TRANSFER", transaction });
  };
};
