import { useState } from "react";
import NiceButton from "../NiceButton/NiceButton";
import sendBtnLogo from "../../assets/icons/send.png";

export const TransactionFund = ({ onTransactionCoins }) => {
  const [transactionAmount, setTransactionAmount] = useState(0);

  const transactionCoins = (ev) => {
    onTransactionCoins(transactionAmount, ev);
  };

  const handleChange = ({ target }) => {
    const value = target.type === "number" ? +target.value : target.value;
    setTransactionAmount(value);
  };

  const IconSendBtn = () => <img src={sendBtnLogo} alt="" />;

  return (
    <section className="transaction-fund">
      <form onSubmit={transactionCoins}>
        <label htmlFor="amount">Transaction amount</label>
        <input
          required
          placeholder="Amount"
          min="0"
          id="amount"
          type="number"
          onChange={handleChange}
          name="amount"
          step="0.01"
        />
        <NiceButton
          Icon={IconSendBtn}
          onClick={transactionCoins}
          className={"send-coin-btn"}
        ></NiceButton>
      </form>
    </section>
  );
};
