const INITIAL_STATE = {
  transactions: [],
};

export function transactionReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case "SET_TRANSFERS":
      return {
        ...state,
        transactions: action.transactions,
      };
    case "ADD_TRANSFER":
      if (state.transactions) {
        return {
          ...state,
          transactions: [...state.transactions, action.transaction],
        };
      }
      return {
        ...state,
        transactions: [action.transaction],
      };
    case "CLEAR_TRANSFERS":
      return {
        transactions: null,
      };
    default:
      return state;
  }
}
